import ACTIONS from '../constants/ActionNames';

const outputReducer = (state = {numIterations: 2, output: ''} , action) => {
  // console.log('grammarReducer', state)
  if (action.type === ACTIONS.numIterationsChanged) {
    // process productions
    let numIterations = action.payload;
    let output = numIterations + '';
    return Object.assign({}, state, {numIterations, output});
  }
  return state;
};

export default outputReducer;



